
import { useEffect, useState } from 'react';
import Link from 'next/link';
import DiaryCard from '../components/DiaryCard/DiaryCard';

type Diary = {
  id: number;
  title: string;
  content: string;
  created_at: string;
};

const MainPage = () => {
  const [diaries, setDiaries] = useState<Diary[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // 日記データを取得する関数
  const fetchDiaries = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/diaries/');
      if (!response.ok) {
        throw new Error('日記データの取得に失敗しました');
      }

      const data = await response.json();
      setDiaries(data);
    } catch (err: any) {
      setError(err.message || 'エラーが発生しました');
    } finally {
      setLoading(false);
    }
  };

  // 更新処理
  const handleUpdate = (id: number) => {
    alert(`更新する日記のID: ${id}`);
    // 更新処理の実装
  };

  // 削除処理
  const handleDelete = async (id: number) => {
    const confirmDelete = confirm(`日記(ID: ${id})を削除しますか？`);
    if (!confirmDelete) return;

    try {
      const response = await fetch(`/api/diaries/${id}`, {
        method: 'DELETE',
      });
      if (!response.ok) {
        throw new Error('削除に失敗しました');
      }

      alert(`日記(ID: ${id})を削除しました`);
      fetchDiaries(); // データ再取得
    } catch (err: any) {
      alert(err.message || '削除中にエラーが発生しました');
    }
  };

  // 初回レンダリング時にデータを取得
  useEffect(() => {
    fetchDiaries();
  }, []);

  return (
    <div className="p-3">
      <header className="flex justify-between items-center">
        <h1 className="text-2xl font-bold flex items-center">日報</h1>
        <Link
          href="/diaries/new"
          className="bg-blue-500 text-white font-bold py-2 px-4 rounded-lg shadow-md hover:bg-blue-600 hover:shadow-lg active:scale-95 transition-transform duration-200 flex items-center"
        >
          登録
        </Link>
      </header>

      {/* ローディング中の表示 */}
      {loading && <p className="text-gray-500">データを読み込み中...</p>}

      {/* エラー表示 */}
      {error && <p className="text-red-500">{error}</p>}

      {/* 日記カードのリスト */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 p-4">
        {diaries.map((diary) => (
          <DiaryCard
            key={diary.id}
            diary={diary}
            onUpdate={handleUpdate}
            onDelete={handleDelete}
          />
        ))}
      </div>

      {/* データがない場合のメッセージ */}
      {!loading && diaries.length === 0 && (
        <p className="text-gray-500">まだ日記が登録されていません。</p>
      )}
    </div>
  );
};

export default MainPage;
