'use client';

import { useRouter } from 'next/navigation';

type Diary = {
  id: number;
  title: string;
  content: string;
  created_at: string;
};

const DiaryCard = ({ diary, onDelete }: { diary: Diary; onDelete: (id: number) => void }) => {
  const router = useRouter();

  const handleUpdate = () => {
    // 編集ページに遷移
    router.push(`/diaries/edit/${diary.id}`);
  };

  return (
    <div className="bg-white border p-4 rounded shadow-md w-full">
      <h2 className="text-lg font-bold">{diary.title}</h2>
      <p className="text-gray-700 mt-2">{diary.content}</p>
      <p className="text-sm text-gray-500 mt-4">{new Date(diary.created_at).toLocaleString()}</p>
      <div className="flex justify-end mt-4 space-x-2">
        <button
          onClick={handleUpdate}
          className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600"
        >
          更新
        </button>
        <button
          onClick={() => onDelete(diary.id)}
          className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600"
        >
          削除
        </button>
      </div>
    </div>
  );
};

export default DiaryCard;
